.quiz {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width: calc(100% - 20px);
    /* box-shadow: 0px 2px 22px 2px #00000040; */
    border: 1px solid #D9D9D9;
    margin-top: 35px;
    border-radius: 10px;
    text-align: center;
    max-width: 768px;
}

.quiz__title {
    font-family: inherit;
    font-size: 18px;
    font-weight: 700;
}

.quiz__button {
    margin: 35px 0 35px 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: calc(100% - 50px);
    max-width: 310px;
    height: 50px;
    background-color: #000;
    color: #fff;
    border-radius: 10px;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    font-size: 14px;
}

.quiz__button:hover {
    cursor: pointer;
}